
import React, { useState, useEffect } from 'react';
import { supabase } from './supabaseClient';
import { Line, Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const StatCard = ({ title, value, unit }) => (
  <div className="card stat-card">
    <h3 className="stat-card-title">{title}</h3>
    <p className="stat-card-value">{value} <span style={{fontSize: '1.5rem'}}>{unit}</span></p>
  </div>
);

const Dashboard = () => {
  const [weatherData, setWeatherData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);
        const { data, error: supabaseError } = await supabase
          .from('weather')
          .select('*')
          .order('created_at', { ascending: false })
          .limit(100);

        if (supabaseError) {
          console.error("Supabase error:", supabaseError);
          throw new Error(`Błąd Supabase: ${supabaseError.message}. Sprawdź, czy polityka RLS (Row Level Security) dla tabeli 'weather' zezwala na odczyt anonimowy.`);
        }

        setWeatherData(data); // Data is already descending
      } catch (err) {
        console.error("Data fetching error:", err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <div className="loading-indicator">Ładowanie danych...</div>;
  }

  if (error) {
    return <div className="error-message"><h2>Wystąpił błąd</h2><p>{error}</p></div>;
  }

  if (weatherData.length === 0) {
    return <div className="loading-indicator">Brak danych do wyświetlenia.</div>
  }

  const latestData = weatherData[0];
  const chartLabels = weatherData.map(d => new Date(d.created_at).toLocaleString()).reverse();

  const lineChartData = {
    labels: chartLabels,
    datasets: [
      {
        label: 'Temperatura (°C)',
        data: [...weatherData].map(d => d.temp_c).reverse(),
        borderColor: '#E74C3C',
        backgroundColor: 'rgba(231, 76, 60, 0.1)',
        fill: true,
        tension: 0.4
      },
      {
        label: 'Wilgotność (%)',
        data: [...weatherData].map(d => d.hum_pct).reverse(),
        borderColor: '#3498DB',
        backgroundColor: 'rgba(52, 152, 219, 0.1)',
        fill: true,
        tension: 0.4
      },
    ],
  };

  const barChartData = {
    labels: chartLabels,
    datasets: [
        {
            label: 'Opady (wartość surowa)',
            data: [...weatherData].map(d => d.rain_raw).reverse(),
            backgroundColor: '#2ECC71',
        }
    ]
  }

  const chartOptions = { responsive: true, plugins: { legend: { position: 'top' } } };

  return (
    <div>
      <h1>Dashboard Pogodowy</h1>
      
      <div className="main-grid">
        <StatCard title="Aktualna Temperatura" value={latestData.temp_c} unit="°C" />
        <StatCard title="Aktualna Wilgotność" value={latestData.hum_pct} unit="%" />
        <StatCard title="Aktualne Ciśnienie" value={latestData.pressure_hpa} unit="hPa" />

        <div className="card chart-container">
            <h2>Temperatura i Wilgotność</h2>
            <Line options={chartOptions} data={lineChartData} />
        </div>

        <div className="card chart-container">
            <h2>Opady</h2>
            <Bar options={chartOptions} data={barChartData} />
        </div>
      </div>

      <div className="card">
        <h2>Surowe Dane (ostatnie 100 pomiarów)</h2>
        <div className="table-container">
          <table>
            <thead>
              <tr>
                <th>Data</th>
                <th>Temp. (°C)</th>
                <th>Wilgotność (%)</th>
                <th>Ciśnienie (hPa)</th>
                <th>Deszcz (raw)</th>
              </tr>
            </thead>
            <tbody>
              {weatherData.map(d => (
                <tr key={d.id}>
                  <td>{new Date(d.created_at).toLocaleString()}</td>
                  <td>{d.temp_c}</td>
                  <td>{d.hum_pct}</td>
                  <td>{d.pressure_hpa}</td>
                  <td>{d.rain_raw}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
